export const processSalaryFieldAccom = (from, to) => {
    let result = '/ ';
    if (from === 0 && to === 0) {
        result += `  `;
    } else if (from && to) {
        result += `${from} - ${to} `;
    } else if (from) {
        result += ` ${from} `;
    } else if (to) {
        result += `${to} `;
    } else {
        result = ' ';
    }

    return result;
};